		},
		methods: {
			huanusername(index){
				this.isshow = false;
				this.isusername = this.iscaizong[index].caiusername;
			},
			toggleTab: function(tab) {
				
				let that = this;			
				that.huanban = false;
			   that.currentTab = tab; // tab 为当前触发标签页的组件名
				if(tab == 'canvasx'){
					that.huanban = true;
					that.dianz = 1;
					that.currentTab = tab;
					setTimeout(function(){
						that.chus();
					},500)
				}else if(tab == 'zhexian'){
					that.dianz = 2;
				}else if(tab == 'kaijlis'){
					that.dianz = 3;
				}
				
			
			},
			 chus(){
			 	let that = this;
			 	var ids = "";
			for(var i = 0; i < 26; i++) {
				ids += "T" + i + "_" + Math.floor(1 + Math.random() * (9 - 1)) + ",";
			}
			ids = ids.substring(0, ids.length - 1);
			that.CreateLine(ids, 20, "rgb(98, 46, 46)", "canvasdiv", "rgb(98, 46, 46)");

			var idst = "";
			for(var i = 0; i < 26; i++) {
				idst += "S" + i + "_" + Math.floor(1 + Math.random() * (9 - 1)) + ",";
			}
			idst = idst.substring(0, idst.length - 1);
			that.CreateLine(idst, 20, "red", "canvasdiv", "red");

			var sandst = "";
			for(var i = 0; i < 26; i++) {
				sandst += "Sa" + i + "_" + Math.floor(1 + Math.random() * (9 - 1)) + ",";
			}
			sandst = sandst.substring(0, sandst.length - 1);
			that.CreateLine(sandst, 20, "rgb(98, 46, 46)", "canvasdiv", "rgb(98, 46, 46)");

			var sidst = "";
			for(var i = 0; i < 26; i++) {
				sidst += "Si" + i + "_" + Math.floor(1 + Math.random() * (9 - 1)) + ",";
			}
			sidst = sidst.substring(0, sidst.length - 1);
			that.CreateLine(sidst, 20, "red", "canvasdiv", "red");

			var wudst = "";
			for(var i = 0; i < 26; i++) {
				wudst += "Wu" + i + "_" + Math.floor(1 + Math.random() * (9 - 1)) + ",";
			}
			wudst = wudst.substring(0, wudst.length - 1);
			that.CreateLine(wudst, 20, "rgb(98, 46, 46)", "canvasdiv", "rgb(98, 46, 46)");
			 },
			CreateLine(ids, w, c, div, bg) {
				var list = ids.split(",");
				for(var j = list.length - 1; j > 0; j--) {
					var tid = $("#" + list[j]);
					var fid = $("#" + list[j - 1]);
					var f_width = fid.outerWidth();
					var f_height = fid.outerHeight();
					var f_offset = fid.offset();
					var f_top = f_offset.top;
					var f_left = f_offset.left;
					var t_offset = tid.offset();
					var t_top = t_offset.top;
					var t_left = t_offset.left;
					var cvs_left = Math.min(f_left, t_left);
					var cvs_top = Math.min(f_top, t_top);
					tid.css("background", bg).css("color", "#fff");
					fid.css("background", bg).css("color", "#fff");
					var cvs = document.createElement("canvas");
					cvs.width = Math.abs(f_left - t_left) < w ? w : Math.abs(f_left - t_left);
					cvs.height = Math.abs(f_top - t_top);
					cvs.style.top = cvs_top + parseInt(f_height / 2) + "px";
					cvs.style.left = cvs_left + parseInt(f_width / 2) + "px";
					cvs.style.position = "absolute";
					var cxt = cvs.getContext("2d");
					cxt.save();
					cxt.strokeStyle = c;
					cxt.lineWidth = 1;
					cxt.lineJoin = "round";
					cxt.beginPath();
					cxt.moveTo(f_left - cvs_left, f_top - cvs_top);
					cxt.lineTo(t_left - cvs_left, t_top - cvs_top);
					cxt.closePath();
					cxt.stroke();
					cxt.restore();
					$("#" + div).append(cvs);
				}
			}
		},
		mounted: function() {
			let that = this;
			
			if(that.$route.query.lskj == 3){
				that.currentTab = 'kaijlis';
				that.dianz = 3;
			}
			
			
			
			that.chus();
			that.container=$(".container");
		    that.canvas=$('.canvass')[0];
		    that.screenWidth=document.documentElement.clientWidth;
		    that.screenHeight=document.documentElement.clientHeight;
		    that.width=1250;
		    that.height=600;
		    

		    
		    that.canvas.width=that.width;
		    that.canvas.height=that.height;
		    that.obj=that.canvas.getContext("2d");
		    that.typechoose=$(".type li");
		    that.colorchoose='red'
		    that.widthchoose="1"
		    that.ding=$("#ding");
		    that.shezhi=$(".shezhi");
		    that.clear=$(".clearr");
		    that.xinjian=$(".xinjian");
		    that.cutflag=false;
		    that.iscut=true;
		    that.color="#000";
		    that.type="line";
		    that.n=3;
		    that.linewidth="1";
		    that.style="stroke";
		    that.arr=[];
		    
		    
		    
		    
		    
		    
   $(".type li").each(function(index,ele){
        $(ele).click(function(){
            that.typechoose.removeClass("typeactive");
            $(this).toggleClass("typeactive");
            that.type=$(this).attr("data");
            if($(this).is(".line")||$(this).is(".pen")){
                that.style="stroke";
                $(".stroke").addClass("styleactive");
                $(".fill").css({display:"none"}).removeClass("styleactive");
            }else{
                $(".fill").css({display:"block"});
            }
        })
    })
    // 清除
    that.clear.click(function(){
        that.arr=[];
        that.obj.clearRect(0,0,that.width,that.height);
    })
  
   //多边形边数

    var x,y,w,h;
    var lx,ly,lw,lh;
    var cutdata;
    that.canvas.onmousedown=function(e){
        x=e.offsetX;
        y=e.offsetY;
        if(that.type=="pen"){
            that.obj.beginPath();
            that.obj.moveTo(x,y);
        }
        if(that.type=="eraser"){
            that.obj.clearRect(x-5,y-5,10,10);
        }
        if(that.cutflag&&type=="cut"){
            if(that.arr.length!=0){
                that.arr.splice(-1,1);
            }
        }
        var draw=new Draw(that.obj,{type:that.style,color:that.color,width:that.linewidth});//实例化构造函数
        that.canvas.onmousemove=function(e){
            w=e.offsetX;
            h=e.offsetY;
            if(that.type!="eraser"){
                that.obj.clearRect(0,0,that.width,that.height);
                if(that.arr.length!=0){
                    that.obj.putImageData(that.arr[that.arr.length-1],0,0,0,0,that.width,that.height);
                }
            }
            if(that.cutflag&&that.type=="cut"){
                if(iscut){
                    that.obj.clearRect(lx,ly,lw-lx,lh-ly);
                }
                var nx=lx+(w-x);
                var ny=ly+(h-y);
                that.obj.putImageData(cutdata,nx,ny);
            }else{
                draw[that.type](x,y,w,h);
            }

        }
        document.onmouseup=function(){
            that.canvas.onmousemove=null;
            document.onmouseup=null;
            if(that.type=="cut"){
                if(!cutflag){
                    cutflag=true;
                    cutdata=obj.getImageData(x+1,y+1,w-x-2,h-y-2);
                    lx=x;ly=y;lw=w;lh=h;
                    container.css({display:"none"});
                }else{
                    cutflag=false;
                    container.css({display:"block"});
                }
            }
            that.arr.push(that.obj.getImageData(0,0,that.width,that.height));
        }
    }		    
		    
		    
		    
		    
		    
		    
			
		},
		components: {
       		 canvasx,
       		 zhexian,
       		 kaijlis
   		 }

	}
</script>
