<template>
	<div class="charts" >
	     <div class="kxiant">
	     	<div id="myChart" style="width:1250px;height:600px"></div>
	     </div>
	</div>
</template>
<script type="text/javascript">	
export default {
  name: 'hello',
  data () {
    return {
   
    }
  },
  mounted(){
    this.drawLine();
  },
  methods: {
    drawLine(){
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 绘制图表
        myChart.setOption({
            title: {
        text: '重庆时时彩k线图',
        x: 'center',
        subtext: '重庆时时彩  第20180114094期  ~ 第20180115023期',
    },
    tooltip: {
        trigger: 'axis',
         formatter: function(params) {
                    var openCode = params[0].data.openCode;

                    var res = params[0].seriesName;
                    res += '<br/>期号：' + params[0].name;
                    res += '<br/>号码：' + params[0].data;
                    return res;
                }
    },
    legend: {
        data:['万位','千位','百位','十位','个位'],
        y: 550
    },
    grid: {
        left: '3%',
        right: '4%',
        bottom: '10%',
        containLabel: true,
    },
    xAxis: {
    	axisLabel: {
                    rotate: -60,
                    interval: 0
                },
        type: 'category',
        boundaryGap: false,
        data: ['201501121112','201501121112','201501121112','201501121112','201501121112','201501121112','201501121112']
    },
    yAxis: {
        type: 'value',
        name:'号码'
    },
    series: [
        {
            name:'万位',
            type:'line',
            data:[1, 5, 8, 6, 2, 2, 5]
        },
        {
            name:'千位',
            type:'line',
            data:[1, 4, 8, 5, 2, 2, 9]
        },
        {
            name:'百位',
            type:'line',
            data:[1, 5, 5, 2, 2, 5, 6]
        },
        {
            name:'十位',
            type:'line',
            data:[1, 5, 6, 2, 8, 4, 3]
        },
        {
            name:'个位',
            type:'line',
            data:[1, 5, 8, 5, 7, 1, 8]
        }
    ]
        });
    }
  }
}
</script>








